import { validateEmail, validateMobile } from '@/utils'

export const rules = {
  userName: [
    {
      required: true,
      message: '请输入用户名',
      trigger: 'blur',
    },
  ],
  password: [
    {
      required: true,
      message: '请输入密码',
      trigger: 'blur',
    },
  ],
  mobile: [
    { required: true, message: '请输入手机', trigger: 'blur' },
    { validator: validateMobile, trigger: 'blur' }
  ],
  email: [
    { required: true, message: '请输入邮箱', trigger: 'blur' },
    { validator: validateEmail, trigger: 'blur' }
  ],
  roleId: [
    {
      required: true,
      message: '请选择角色',
      trigger: 'blur',
    },
  ]
}
