<template>
  <el-dialog
    title="添加角色"
    v-model="dialogVisible"
    width="30%"
    :before-close="handleClose"
  >
    <el-form ref="formRef" :model="form" :rules="rules" style="width: 100%;" label-position="top">
      <el-form-item label="用户名" prop="userName">
        <el-input v-model.trim="form.userName" clearable placeholder="请输入用户名" />
      </el-form-item>
      <el-form-item style="margin-bottom: 30px;" label="密码" prop="password">
        <el-input v-model.trim="form.password" type="password" clearable placeholder="请输入密码" />
      </el-form-item>
      <el-form-item style="margin-bottom: 30px;" label="手机" prop="mobile">
        <el-input v-model.trim="form.mobile" clearable placeholder="请输入手机" />
      </el-form-item>
      <el-form-item style="margin-bottom: 30px;" label="邮箱" prop="email">
        <el-input v-model.trim="form.email" clearable placeholder="请输入邮箱" />
      </el-form-item>
      <el-form-item prop="roleId" label="角色">
        <el-select v-model="form.roleId" style="width: 100%;" placeholder="请选择角色">
          <el-option
            v-for="item in roleList"
            :key="item.id"
            :label="item.roleName"
            :value="item.id"
          />
        </el-select>
      </el-form-item>
    </el-form>
    <div class="footer">
      <el-button @click="handleClose">取消</el-button>
      <el-button type="primary" @click="addHandle">保存</el-button>
    </div>
  </el-dialog>
</template>
<script>
import apis from '@/apis'
import { ref, reactive, watch } from 'vue'
import { ElMessage } from 'element-plus'
import { rules } from './config'
export default {
  props: {
    roleList: Array,
    visible: Boolean,
    detail: Object
  },
  setup(props, { emit }) {
    const { addUser, updateUser } = apis
    const formRef = ref()
    const dialogVisible = ref(false)
    const form = reactive({
      userName: '',
      password: '',
      mobile: '',
      email: '',
      roleId: '',
    })
    const handleClose = () => {
      formRef.value && formRef.value.resetFields()
      emit('handleClose')
    }
    const addHandle = () => {
      formRef.value && formRef.value.validate(val => {
        if (val) {
          const { id } = props?.detail ?? {}
          if (!id) {
            addUser(form).then(res => {
              if (res?.success) {
                ElMessage.success('新增成功')
                emit('successHandle')
                handleClose()
              }
            })
          } else {
            updateUser({ ...form, id }).then(res => {
              if (res?.success) {
                ElMessage.success('编辑成功')
                emit('successHandle')
                handleClose()
              }
            })
          }
        }
      })
    }
    watch(
      () => props.visible,
      val => {
        dialogVisible.value = val
        if (val) {
          const { userName, password, roleId, mobile, email } = props.detail ?? {}
          form.userName = userName
          form.password = password
          form.email = email
          form.mobile = mobile
          form.roleId = roleId
        }
      }
    )
    return {
      form,
      formRef,
      rules,
      dialogVisible,
      addHandle,
      handleClose,
    }
  },
}
</script>
<style lang="scss" scoped>
.footer {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
