<template>
  <div style="width: 100%;">
    <div class="top">
      <el-button type="primary" @click="addHandle">新增</el-button>
    </div>
    <el-table
      v-loading="loading"
      :data="dataSource"
      style="width: 100%"
    >
      <el-table-column prop="userName" label="账号" />
      <el-table-column prop="mobile" label="手机号" />
      <el-table-column prop="email" label="邮箱" />
      <el-table-column prop="gmtCreate" label="创建时间" />
      <el-table-column prop="roleId" label="状态">
        <template #default="{row}">
          <div>
            {{ row.userStatus === 1 ? '启用' : row.userStatus === 0 ? '禁用' : '- -' }}
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="roleId" label="角色">
        <template #default="{row}">
          <div>
            {{ roleList?.find(item => item.id === row.roleId)?.roleName ?? '- -' }}
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="oprate" label="操作" width="200">
        <template #default="{row}">
          <div>
            <el-link style="margin-right: 10px;" type="primary" @click="editHandle(row)">编辑</el-link>
            <el-link type="primary" @click="statusHandle(row)">{{ row.userStatus === 1 ? '禁用' : row.userStatus === 0 ? '启用' : '- -' }}</el-link>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <div class="footer">
      <el-pagination
        layout="prev, pager, next"
        :total="total"
        v-model:current-page="currentPage"
        :page-size="pageSize"
        @current-change="handleCurrentChange"
      />
    </div>
    <Modal
      :roleList="roleList"
      :detail="detail"
      :visible="visible"
      @handleClose="handleClose"
      @successHandle="successHandle"
    />
  </div>
</template>
<script>
import { ref } from 'vue'
import apis from '@/apis'
import Modal from './modal'
import { ElMessage } from 'element-plus'

export default {
  components: {
    Modal
  },
  setup() {
    const { getUser, getRioleList, updateUserStatus } = apis
    const loading = ref(false)
    const visible = ref(false)
    const detail = ref(null)
    const currentPage = ref(1)
    const pageSize = ref(10)
    const total = ref(0)
    const roleList = ref([])
    const dataSource = ref([])
    getRioleList().then(res=> {
      if (res?.success) {
        roleList.value = res?.data ?? []
      }
    })
    const _getUser = (params) => {
      loading.value = true
      getUser(params).then(res => {
        const { success, data } = res ?? {}
        if (success) {
          dataSource.value = data?.records ?? []
          total.value = data?.total
          currentPage.value = data.current
        }
        loading.value = false
      })
    }
    const getParams = () => {
      return {
        currentPage: currentPage.value,
        pageSize: pageSize.value
      }
    }
    _getUser({ ...getParams() })
    const handleCurrentChange = val => {
      const { currentPage: o, ...others } = getParams()
      _getUser({ currentPage: val, ...others })
    }
    const successHandle = () => {
      _getUser({ ...getParams() })
    }
    const addHandle = () => {
      visible.value = true
    }
    const handleClose = () => [
      visible.value = false
    ]
    const editHandle = row => {
      visible.value = true
      detail.value = row
    }
    const statusHandle = row => {
      const params = { id: row?.id, userStatus: row?.userStatus === 0 ? 1 : 0 }
      updateUserStatus(params).then(res => {
        res?.success && _getUser({ ...getParams() })
      })
    }

    return {
      loading,
      statusHandle,
      pageSize,
      currentPage,
      total,
      roleList,
      visible,
      detail,
      dataSource,
      handleClose,
      addHandle,
      editHandle,
      handleCurrentChange,
      successHandle
    }
  },
}
</script>
<style lang="scss" scoped>
@import "./index.scss";
</style>
